.jumbotron-heading {
  font-family: 'Playfair Display', serif; }

.jumbotron {
  width: 100%;
  background: var(--dodger-blue);
  color: white;
  padding: calc(3 * 1.5rem) 0;
  text-align: center;
  position: relative;
  overflow: hidden;
  z-index: 1000; }

.jumbotron-heading {
  text-shadow: 0 3px rgba(141, 255, 239, 0.4), 0 5px rgba(141, 255, 239, 0.4), 0 7px rgba(141, 255, 239, 0.4);
  font-size: var(--scale-500);
  line-height: calc(5 * 1.5rem); }

@media (max-width: 640px) {
  .jumbotron-heading {
    text-shadow: 0 1px rgba(141, 255, 239, 0.4), 0 2px rgba(141, 255, 239, 0.4), 0 3px rgba(141, 255, 239, 0.4), 0 4px rgba(141, 255, 239, 0.4), 0 5px rgba(141, 255, 239, 0.4);
    font-size: var(--scale-400);
    line-height: calc(4 * 1.5rem); } }

.jumbotron-subheading {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 300;
  text-align: center;
  width: 100%;
  max-width: 640px;
  margin: auto; }



