:root {
  --alabaster: #F7F7F7;
  --gallery: #EEEEEE;
  --gray: #888888;
  --dodger-blue: #5873FF;
  --green-white: #EBEBDF;
  --husk: #B3AF44;
  --woodland: #374620;
  --mine-shaft: #252525;
  --aqua-marine: #8DFFEF; }

:root {
  --header-height: 3.375rem;
  --navbar-height: 3.375rem;
  --body-height: 1fr;
  --footer-height: 3.375rem;
  --scale-600: 7.594em;
  --scale-500: 5.063rem;
  --scale-400: 3.375rem;
  --scale-300: 2.25rem;
  --scale-200: 1.5rem;
  --scale-100: 1rem;
  --scale-75: 0.667rem;
  --scale-50: 0.444rem;
  --scale-25: 0.296rem;
  --base-line-height: 1.5rem; }

body {
  font-family: 'Poppins', -apple-system, system-ui, avenir, "lucida grande", "gill sans", "helvetica neue", helvetica, sans-serif;
  background: var(--alabaster);
  color: var(--mine-shaft); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

img {
  -webkit-box-sizing: content-box;
          box-sizing: content-box; }

h1 {
  font-size: var(--scale-400);
  line-height: calc(4 * 1.5rem); }

h2 {
  font-size: var(--scale-300);
  line-height: calc(3 * 1.5rem); }

h3 {
  font-size: var(--scale-200);
  line-height: calc(2 * 1.5rem); }

h4, body, p {
  font-size: var(--scale-100);
  line-height: calc(1 * 1.5rem); }

h5, small {
  font-size: var(--scale-75);
  line-height: calc(1 * 1.5rem); }

h6 {
  font-size: var(--scale-50);
  line-height: calc(1 * 1.5rem); }



