.button-holder {
  display: inline-block;
  color: white; }

.button {
  background: -webkit-linear-gradient(60deg, rgba(255, 255, 255, 0.2), rgba(141, 255, 239, 0.4)), var(--dodger-blue);
  background: -o-linear-gradient(60deg, rgba(255, 255, 255, 0.2), rgba(141, 255, 239, 0.4)), var(--dodger-blue);
  background: linear-gradient(30deg, rgba(255, 255, 255, 0.2), rgba(141, 255, 239, 0.4)), var(--dodger-blue);
  display: inline-block;
  padding: 0 3rem;
  height: 3rem;
  line-height: 3rem;
  border-radius: 1.5rem;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  position: relative;
  z-index: 1000; }

.button:hover {
  background: -webkit-linear-gradient(60deg, rgba(255, 255, 255, 0.4), rgba(141, 255, 239, 0.6)), var(--dodger-blue);
  background: -o-linear-gradient(60deg, rgba(255, 255, 255, 0.4), rgba(141, 255, 239, 0.6)), var(--dodger-blue);
  background: linear-gradient(30deg, rgba(255, 255, 255, 0.4), rgba(141, 255, 239, 0.6)), var(--dodger-blue); }



